body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.practice-styles {
  background-color: hsla(0, 100%, 50%, 0.3);
  width: 300px;
  height: 400px;
  padding-top: 10px;
  position: relative;
}

.header {
  padding: 10px 0;
  border-bottom: 1px solid white;
  width: 70%;
  margin: 0 auto;
  color: #fff;
  font-size: 24px;
  letter-spacing: .2em;
  padding: 5px;
}

.reminder_set {
  padding: 10px 0;
  width: 95%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reminder_set .reminder_save {
  margin: 0 15%;
}

.timers-container {
	background-color: white;
	padding: 15px;
  margin: 5px 5px;
  height: 60%;
  overflow-y: scroll;
}

.timer-tile {
  background-color: black;
}

.timer-tile {
	background: white;
	padding: 10px 15px;
	margin-bottom: 15px;
	border-radius: 8px;
}
.timer-tile-container {
	background-color: white;
	display: flex;
	flex-direction: row;
	margin-bottom: 15px;
}
.timer-information {
	width: 60%;
}
.timer-information .title {
	font-size: 1.25em;
	line-height: 1.3;
	text-align: left;
	margin-bottom: 7px;
}
.timer-information .details {
	text-transform: uppercase;
	letter-spacing: 1px;
	line-height: 1.15;
	font-size: 0.75em;
	text-align: left;
}
.timer-countdown {
	width: 40%;
}
.timer-countdown div {
	font-size: 1.85em;
	text-align: right;
}
.timers-container {
	background-color: rgb(255, 190, 187);
}
.no-timers {
	color: #fff;
	letter-spacing: 0.15em;
}
.timer-removeButton {
	width: 55%;
	padding: 10px 0;
	color: #fff;
	text-transform: uppercase;
	letter-spacing: 0.1em;
	font-size: 12px;
	background-color: rgb(255, 117, 110);
	border: none;
	cursor: pointer;
	border-radius: 30px;
}
.reminder_button{
  width: 55%;
  padding: 10px 0;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .1em;
  font-size: 12px;
  background-color: rgb(255,190,187);
  border: 1px solid white;
  cursor: pointer;
  border-radius: 30px;
  margin-top: 5px;
  box-shadow:5px 5px 15px hsla(0, 100%, 30%, 0.3);
  position: relative;
}

.reminder_button:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.reminder_button:active, .reminder_button:focus {
  outline: 0;
  -moz-outline-style: none;
}

.reminder_form {
  background-color: #fff;
  width: 85%;
  margin-top: 20px;
  padding: 10px;
  border-radius: 20px;
  box-shadow:5px 5px 15px hsla(0, 100%, 30%, 0.3);
  padding-bottom: 15px;
}


